<template>
  <div id="fontAwesome">
    <img :src="imgPath" alt="" />

    <div id="fontImgContainer"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      imgPath: require("@/assets/aoa.gif"),
    };
  },
  mounted() {
    function createIcons(g) {
      var faIcons = [
        "fa-heart",
        "fa-star",
        "fa-glass-martini-alt",
        "fa-grin-beam-sweat",
        "fa-grin-hearts",
        "fa-gem",
        "fa-umbrella",
        "fa-gripfire",
        "fa-snowflake",
        "fa-ghost",
        "fa-wind",
      ];
      //get a random icons from the list
      var r = Math.floor(Math.random() * faIcons.length);

      //get random distance within the width
      var rw = Math.floor(Math.random() * 100);

      //give random color
      var rc = Math.floor(Math.random() * 256);
      var gc = Math.floor(Math.random() * 256);
      var bc = Math.floor(Math.random() * 256);

      //create the icons
      document.getElementById("fontImgContainer").innerHTML +=
        '<i id="fas' +
        g +
        '" class="fas ' +
        faIcons[r] +
        '" style="left:' +
        rw +
        "%; color:rgb(" +
        rc +
        "," +
        gc +
        "," +
        bc +
        ');"></i>';

      //**************************************
      //animetion move up
      //**************************************

      //get the height of the container
      var h = document.getElementById("fontAwesome").offsetHeight;

      //the ID
      var nameIcon = "fas" + g;


      console.log(nameIcon);

      // random start point at y-axe
      var startPoint = Math.floor(Math.random() * 442);
      var pos = 0 - startPoint;

      setInterval(frame, 40);

      function frame() {
        if (pos > h) {
          var elem = document.getElementById(nameIcon);

          //NEW random distance within the width
          rw = Math.floor(Math.random() * 100);

          //NEW random color
          rc = Math.floor(Math.random() * 256);
          gc = Math.floor(Math.random() * 256);
          bc = Math.floor(Math.random() * 256);

          elem.style.color = "rgb(" + rc + "," + gc + "," + bc + ")";
          elem.style.left = rw + "%";
          elem.style.bottom = 0 + "px";
          pos = 0;
        } else {
          pos++;
          h = document.getElementById("fontAwesome").offsetHeight;
          var elem = document.getElementById(nameIcon);
          elem.style.bottom = pos + "%";
          // elem.style.left = pos + 'px';
        }
      }
    }

    //create all the icons in this loop !!!
    for (let index = 0; index < 33; index++) {
      createIcons(index);
    }


  },
};
</script>

<style>
#fontAwesome {
  position: relative;
  height: 100%;
  overflow: hidden;
}

#fontImgContainer {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 23;
}

#fontAwesome img {
  object-fit: contain;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  filter: grayscale(70%);
}

.fas {
  position: absolute;
  font-size: 20px;
  z-index: 25;
}
</style>