<template>
  <div id="div90Degree">
    <div id="run90" class="container90">
      <div v-for="x in 64" :key="x" class="rotate90"></div>
    </div>

    <class class="headlineContainer">
      <class class="headlinerInner">
        <div class="spinContainer">
          <h1>THE</h1>
          <h1 class="nightlif">NIGHTLIF<span id="appendContent">E</span></h1>
        </div>

        <h1 class="ofTokyo">OF TOKYO</h1>
      </class>
    </class>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // img1: require("@/assets/rotate90_img/b1.jpg"),
      // img2: require("@/assets/rotate90_img/b2.jpg"),
      // img3: require("@/assets/rotate90_img/b3.jpg"),
      // img4: require("@/assets/rotate90_img/b4.jpg"),
      // img5: require("@/assets/rotate90_img/b5.jpg"),
      // img6: require("@/assets/rotate90_img/b6.jpg"),
      // img7: require("@/assets/rotate90_img/b7.jpg"),
    };
  },
  mounted() {
    var childDivs = document.getElementById("run90").getElementsByTagName("div");

    var addCount = 1;
    var removeCount = 0;

    function movingNoise() {

      childDivs[addCount].classList.add("addNoise");
      addCount++;


      childDivs[removeCount].classList.remove("addNoise"); 
      removeCount++;

      if (addCount > 63) {
          addCount = 1;
          removeCount = 0;
      }

      setTimeout(movingNoise, 240);
    }
    movingNoise();



  },
};
</script>

