<template>
  <div id="JapanRandomText">
    <div class="imgContainer">
      <div id="appendContainer" class="writeContainer">
       
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  mounted() {
    function startTyping() {
      var textArray = [
        ["Cherry Blossom ", " "],
        ["Fearless ", " "],
        ["Snow ", " "],
        ["Love ", " "],
        ["Rebel ", " "],
        ["Flower ", " "],
        ["Bankai ", " "],
        ["Cute ", " "],
        ["Kamehameha ", " "],
        ["Sugoi ", " "],
        ["Senpai ", " "],
        ["Pirate ", " "],
        ["Oniisan ", " "],
        ["Daijbu ", " "],
        ["Otaku ", " "],
        ["Speed ", " "],
        ["Fire ", " "],
        ["Konnichiwa ", " "],
        ["Shinigami ", " "],
        ["Invasion ", " "],
        ["Dragon ", " "],
        ["Princess ", " "],
        ["Hadouken ", " "],
        ["Super ", " "],
        ["Enemy ", " "],
        ["Revenge ", " "],
      ];

      // fill the array up with value so you can create more
      var createText = [];
      for (let i = 0; i < textArray.length; i++) {
        createText[i] = [0, "", "", 0, 0];
      }

      //create all the divs
      for (let i = 0; i < textArray.length; i++) {
        document.getElementById("appendContainer").innerHTML +=
          '<h3 class="typing"></h3>';
      }

      // the main function
      function myLoop(h, x, dx, dy) {
        setTimeout(function () {
          if (h[x][3] === 0 || h[x][3] === 1) {
            // add letters
            if (h[x][4] < textArray[x][h[x][3]].length) {
              h[x][1] = textArray[x][h[x][3]];

              h[x][2] = h[x][1].slice(0, ++h[x][0]);
              document.getElementsByClassName("typing")[x].textContent =
                h[x][2];

              document.getElementsByClassName("typing")[x].style.right =
                dx + "%";
              document.getElementsByClassName("typing")[x].style.top = dy + "%";

              //infinity loop
              if (h[x][3] === 1 && h[x][2] === textArray[x][h[x][3]]) {
                //reset

                h[x][0] = 0;
                h[x][1] = "";
                h[x][2] = "";
                h[x][3] = 0;
                h[x][4] = -1;
              }
            }
          }

          if (h[x][3] === 0) {
            //subtrack letter
            if (h[x][4] >= textArray[x][h[x][3]].length) {
              h[x][1] = textArray[x][h[x][3]];
              h[x][2] = h[x][1].slice(0, --h[x][0]);
              document.getElementsByClassName("typing")[x].textContent =
                h[x][2];
              // subtrack only the first word in the array
              if (h[x][2].length === 0) {
                if (h[x][3] === 0) {
                  h[x][3]++;
                  h[x][4] = -1;
                }
              }
            }
          }

          h[x][4]++;
          if (h[x][4] < textArray[0][h[x][3]].length * 2) {
            myLoop(h, x, dx, dy);
          }
        }, 550);
      }

      var distenceX = 0;
      var distenceY = 0;
      var addToDistenceY = 90 / textArray.length;

      //loop to create all the texts
      for (let index = 0; index < createText.length; index++) {
        var getR = Math.floor(Math.random() * 65);
        distenceX = getR;
        distenceY += addToDistenceY;

        myLoop(createText, index, distenceX, distenceY);
      }
    }

    startTyping();

    // // YES  YES  YES  YES  YES  YES  YES  YES  YES  YES  YES  YES  YES  YES  YES  YES YES

    //  //**********************myLoop(0);************************
    //     let index = 0;
    //     let currentText = "";
    //     let letter = "";
    //     let count = 0;
    //     let i = 0;
    //     function myLoop(x) {

    //       setTimeout(function () {
    //         if (count === 0 || count === 1) {
    //           // add letters
    //           if (i < textArray[x][count].length) {
    //             currentText = textArray[x][count];
    //             letter = currentText.slice(0, ++index);
    //             document.getElementsByClassName("typing")[x].textContent = letter;

    //           }
    //         }

    //         if (count === 0) {
    //           //subtrack letter
    //           if (i >= textArray[x][count].length) {
    //             currentText = textArray[x][count];
    //             letter = currentText.slice(0, --index);
    //             document.getElementsByClassName("typing")[x].textContent = letter;
    //             // subtrack only the first word in the array
    //             if (letter.length === 0) {
    //               if (count === 0) {
    //                 count++;
    //                 i = -1;
    //               }
    //             }
    //           }
    //         }

    //         i++;
    //         if (i < textArray[0][count].length * 2) {
    //           myLoop(x);
    //         }
    //       }, 400);
    //     }
    //     myLoop(0);

  },
};
</script>

