  height: 100%;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("~@/assets/kpop.jpg");
}

/*
START - Random TEXT
*/

.writeContainer {
  height: 100%;
  width: 100%;
  position: relative;
}

.typing {
  position: absolute;
}

